<template>
  <div class="form-item">
    <div class="form-item-label">{{ label }}</div>
    <div class="form-item-input">
      <input :value="modelValue" @input="handleChange" v-bind="$attrs" />
    </div>
  </div>
</template>
<script setup>
import { defineEmits, defineProps } from 'vue'
defineProps({
  label: {
    type: String
  },
  modelValue: {
    type: String,
    default: undefined
  }
})

const emits = defineEmits(['update:modelValue'])

const handleChange = (e) => {
  emits('update:modelValue', e.target.value)
}
</script>
<style lang="less" scoped>
.form-item {
  margin-bottom: 12px;
  display: flex;
  &-label {
    flex: 0 0 120px;
    text-align: right;
    margin-right: 10px;
  }
  &-input {
    input {
    }
  }
}
</style>
