<template>
  <div class="input-config">
    <FormInputItem label="标题" v-model="data.title"></FormInputItem>
    <FormInputItem label="姓名" v-model="data.userName"></FormInputItem>
    <FormInputItem label="地区" v-model="data.address"></FormInputItem>
    <FormInputItem label="内容" v-model="data.memo"></FormInputItem>
  </div>
</template>
<script setup>
import { reactive, watch } from 'vue'
import FormInputItem from './FormInputItem.vue'
import { useAppStore } from '@/store/app'
const appStore = useAppStore()
const data = reactive({
  title: '',
  userName: '',
  address: '',
  memo: ''
})

watch(
  () => data,
  () => {
    appStore.SETDATA(data)
  },
  {
    deep: true
  }
)
</script>
