import { defineStore } from 'pinia'

export const useAppStore = defineStore('main', {
  state: () => {
    return {
      // 所有这些属性都将自动推断其类型
      data: {
        title: '前端是否需要单元测试？收益如何？',
        userName: '一只无处安放的小鸟',
        address: '火星',
        memo: '微信签名好像用了8.9年了吧，当有微信开始就一直是那句话，就没有变过'
      }
    }
  },
  actions: {
    SETDATA (data) {
      this.data = data
    }
  }
})
