<template>
  <div class="detail">
    <div class="header">
      世界尽头的咖啡馆
    </div>
    <div class="title">
      {{ data.title }}
    </div>
    <div class="detail-content">
      <img src="/chatdemo/images/1.jpg" class="left" />
      <div class="right">
        <div class="top">
          {{ data.userName }} &nbsp;{{ "06-30" }}&nbsp;{{ data.address }}
          <div class="icons">
            <Icon icon="icon-dianzan">520</Icon>
            <Icon icon="icon-fandui">1</Icon>
            <Icon icon="icon-fenxiang">520</Icon>
          </div>
        </div>
        <div class="memo">
          {{ data.memo }}
        </div>
      </div>
    </div>
  </div>
  <button @click="download" style="margin-top: 10px">下载图片</button>
</template>
<script setup>
import { useAppStore } from '@/store/app'
import { computed } from 'vue'
import Icon from './Icon.vue'
import html2canvas from 'html2canvas'

const appStore = useAppStore()
const data = computed(() => {
  return appStore.$state.data
})

const download = () => {
  html2canvas(document.querySelector('.detail')).then((canvas) => {
    const dataURL = canvas.toDataURL('image/png')
    const a = document.createElement('a')
    a.download = '图片.png'
    a.href = dataURL
    a.click()
  })
}
</script>
<style lang="less" scoped>
.detail {
  padding: 30px 20px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  text-align: left;
  color: black;
  width: 500px;
  box-sizing: border-box;
  .header {
    font-size: 18px;
  }
  .title {
    margin-top: 15px;
    font-size: 16px;
  }
  &-content {
    width: 100%;
    display: flex;
    .left {
      width: 30x;
      height: 30px;
      margin-top: 18px;
      margin-right: 10px;
      border-radius: 5px;
    }
    .right {
      flex: 1;
      .top {
        font-size: 14px;
        color: gray;
        margin-top: 25px;
        font-size: 12px;
        position: relative;
        .icons {
          position: absolute;
          right: 0px;
          top: -6px;
        }
      }
      .memo {
        margin-top: 30px;
        font-size: 15px;
      }
    }
  }
}
</style>
